@import "src/common/styles/sass/variables";
@import "/src/common/styles/sass/mixins";

.myProjectBlock {
  height: 90vh;
  background-color: $bgSecondColor;

  .projects {
    width: 100%;
    height: 60%;
    @include flex(row, center, center, nowrap);
    margin-top: 70px;
    @media screen and (max-width: 1295px) {
      flex-direction: column;
      align-items: center;
    }
  }
}







